import {Component, OnInit, Input, OnChanges, SimpleChanges, Output, EventEmitter} from '@angular/core'
import { Hero } from './hero';
import { HeroService } from './hero.service';

@Component({
  selector:'my-dashboard',
  templateUrl:'dashboard.component.html',
  styleUrls: ['dashboard.component.css']
})
export class DashboardComponent implements OnInit,OnChanges{
  heroes: Hero[] = [];
  focu:boolean = false;

  @Input() keyword:string;

  @Output() setSearch: EventEmitter<Hero> = new EventEmitter();

  constructor(private heroService: HeroService) { }

  ngOnInit(): void {
    this.heroService.getHeroes()
      .then((heroes) => {
        this.heroes = heroes.slice(1, 5)
      });
  }
  ngOnChanges(changes: SimpleChanges): void {
    // console.log(changes.keyword.currentValue)
    if(this.keyword&&!isNaN(Number(this.keyword))){
      this.heroService.getHero(Number(this.keyword)).then((res) => {
        let hreo:Hero = res
        this.setSearch.emit(hreo)
      })
    }
  }
  focus() {
    return this.focu
  }
}



