import { Component,OnInit } from '@angular/core';
import {Hero} from './hero'
import { HeroService } from './hero.service';
import {Router, ActivatedRoute} from '@angular/router'
@Component({
  selector: 'my-heroes',
  templateUrl:'./heroes.component.html',
  styleUrls: ['./heroes.component.css']
})
export class HeroesComponent implements OnInit{
  selectedHero: Hero;
  heroes: Hero[];
  heroesId:number;
  constructor( private router: Router,private heroService: HeroService,private routerInfo: ActivatedRoute) { }

  getHeroes(): void {
    this.heroService.getHeroes().then((heroes) => {
      this.heroes = heroes
    });
  }

  ngOnInit(): void {
    this.getHeroes();
    this.heroesId = this.routerInfo.snapshot.queryParams["id"];
    console.log(this.heroesId)
  }

  onSelect(hero: Hero): void {
    this.selectedHero = hero;
  }

  gotoDetail(): void {
    this.router.navigate(['/first/detail', this.selectedHero.id]);
  }

  add(name: string): void {
    name = name.trim();
    if (!name) { return; }
    this.heroService.create(name)
      .then(hero => {
        this.heroes.unshift(hero);
        this.selectedHero = null;
      });
  }

  delete(hero: Hero): void {
    this.heroService
      .delete(hero.id)
      .then(() => {
        this.heroes = this.heroes.filter(h => h !== hero);
        if (this.selectedHero === hero) { this.selectedHero = null; }
      });
  }
}



