import { Component, OnInit, Input, Output,EventEmitter } from '@angular/core';
import {trigger, state, style, animate, transition,group} from '@angular/animations';
@Component({
  selector: 'app-toast',
  templateUrl: './toast.component.html',
  styleUrls: ['./toast.component.css'],
  animations: [
    trigger('flyInOut', [
      state('in', style({width: 300, transform: 'translateX(0)', opacity: 1})),
      state('out', style({width: 10, transform: 'translateX(150px)', opacity: 0})),
      transition('* => in', [
        style({width: 10, transform: 'translateX(50px)', opacity: 0}),
        group([
          animate('0.3s 0.1s ease', style({
            transform: 'translateX(0)',
            width: 300
          })),
          animate('0.3s ease', style({
            opacity: 1
          }))
        ])
      ]),
      transition('* => out', [
        group([
          animate('0.3s ease', style({
            transform: 'translateX(150px)',
            width: 10
          })),
          animate('0.3s 0.2s ease', style({
            opacity: 0
          }))
        ])
      ])
    ])
  ]
})
export class ToastComponent implements OnInit {
  private flatShow:boolean=false;
  private toastState:string='in';
  @Input() title:string = '温馨提示'
  @Input() cancelBtnText:string = '取消'
  @Input() confirmBtnText:string = '删除'
  @Input() text:string='';
  @Output() cancel:EventEmitter<any> = new EventEmitter ();
  @Output() confirm:EventEmitter<any> = new EventEmitter();
  private obj:any;
  constructor() { }

  ngOnInit() {
   // console.log(window.clientWidth)
  }
  show(obj) {
    this.flatShow = true;
    if(obj){
      this.obj=obj;
    }
  }
  hide() {
    this.flatShow = false;
  }
  cancelToast() {
    this.cancel.emit(this.obj)
    this.toastState='out'
  }
  confirmToast() {
    this.confirm.emit(this.obj)
    this.toastState='out'
  }
  animationStarted(event) {
    //console.log(event)
  }
  animationDone(event){
    if(event.toState=='out'){
      this.toastState='in'
      this.flatShow = false
    }
  }
}



