import {Component, OnInit} from '@angular/core';
import {Router} from "@angular/router";
import {StockService, Stock} from "../stock.service";
import {FormControl} from "@angular/forms";
import 'rxjs/Rx'
import {Observable} from 'rxjs'
import {findIndex,numToArr} from '../../common/fn'

@Component({
  selector: 'app-stock-manage',
  templateUrl: './stock-manage.component.html',
  styleUrls: ['./stock-manage.component.css']
})
export class StockManageComponent implements OnInit {
  private Stocks: Stock[];

  private nameFilter:FormControl = new FormControl();

  private keyWord:string='';

  private total:Array<number> = [];

  private pageSize:number= 3;

  private pageNum:number =1;

  constructor(private router: Router,private stockService:StockService) {

  }

  ngOnInit() {
    this.nameFilter.valueChanges
      .debounceTime(500)
      .subscribe((value) => {
        this.keyWord=value
      })
    this.getList(this.keyWord,this.pageSize,this.pageNum)
  }

  addStock(): void {
    this.router.navigateByUrl('/stock/0')
  }

  upDataStock(stock): void {
    this.router.navigateByUrl('/stock/' + stock.id)
  }

  searchList() {
   this.getList(this.keyWord,this.pageSize,this.pageNum)
  }
  getList(name,pageSize,pageNum) {
    this.stockService.getStocks({name:name,pageSize:pageSize,pageNum:pageNum}).subscribe((res) => {
      this.Stocks = res.data;
      this.pageNum=pageNum;
      this.total = numToArr(Math.ceil(res.total/this.pageSize));
    });
  }
  Prev() {
    this.pageNum--;
    this.getList(this.keyWord,this.pageSize,this.pageNum)
  }
  next() {
    this.pageNum++;
    this.getList(this.keyWord,this.pageSize,this.pageNum)
  }
  deleteStock(stock:Stock,toast:any) {
    toast.show(stock)
  }
  confirm(stock) {
    this.stockService.deleteStock(stock.id)
      .then((res) => {
        this.getList(this.keyWord,this.pageSize,this.pageNum);
      })
  }
}



