import { Component, OnInit } from '@angular/core';
import {Router, NavigationEnd} from '@angular/router'
import 'rxjs/add/operator/filter'
@Component({
  selector: 'app-content',
  templateUrl: './content.component.html',
  styleUrls: ['./content.component.css']
})
export class ContentComponent implements OnInit {
  pageTitle = '';
  pageDesc = '';
  constructor(private router:Router) {
    router.events
      .filter(event => event instanceof NavigationEnd)
      .subscribe((event:NavigationEnd) => {
        if(event.urlAfterRedirects.startsWith('/home')){
          this.pageTitle='首页';
          this.pageDesc='待定'
        }else if(event.urlAfterRedirects.startsWith('/stockManage')){
          this.pageTitle='管理';
          this.pageDesc='股票管理列表'
        }else if(event.urlAfterRedirects.startsWith('/stock')){
          this.pageTitle='管理';
          this.pageDesc='股票详情'
        }else if(event.urlAfterRedirects.startsWith('/first')){
          this.pageTitle='angular';
          this.pageDesc='first-test'
        }
      })
  }

  ngOnInit() {
  }

}



