import { Component, OnInit } from '@angular/core';
import {NgForm} from "@angular/forms";
import {LoginService} from "../login.service";
import {Router} from '@angular/router'
import {ToastrService} from 'ngx-toastr'

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  mobileValid:boolean = true;
  mobilePristine:boolean = true;
  private invalid=true;
  query:any={};
  text:string = '你确定要删除吗？';
  code:number;
  ActiveToast:any;
  constructor(private loginService:LoginService,private router:Router,private toastr:ToastrService) { }

  ngOnInit() {
  }
  onInput1(myForm:NgForm) {
    this.mobileValid=myForm.form.get('mobile').valid;
    this.mobilePristine = myForm.form.get('mobile').pristine;
    this.invalid=myForm.form.invalid;
  }
  onInput2(myForm:NgForm) {
    this.mobileValid=myForm.form.get('password').valid;
    this.mobilePristine = myForm.form.get('password').pristine;
    this.invalid=myForm.form.invalid;
  }
  login(info:any,valid:boolean,toast:any){
    if(valid){
      // console.log(info)
      this.query = {
        pass:info.password,
        mobile:info.mobile
      }
      this.loginService.login(this.query)
        .subscribe((res) => {
          if(res.code == 2012){
            this.code = 2102;
            this.text=res.msg;
            this.ActiveToast=this.toastr.warning(res.msg)
            this.ActiveToast&&this.ActiveToast.onHidden
              .subscribe((res) => {
                this.router.navigateByUrl('/register')
              })
          }else if(res.code == 0){
            this.code = 0;
            this.text='登陆成功';
            this.ActiveToast=this.toastr.success(res.msg)
            this.ActiveToast&&this.ActiveToast.onHidden
              .subscribe((data) => {
                this.router.navigateByUrl('/stockManage')
                localStorage.setItem('mobile',res.data.mobile)
              })
          }else{
            this.text=res.msg;
            this.ActiveToast=this.toastr.error(res.msg)
          }
          // toast.show()
        })
    }
  }
  confirm(bl) {
    console.log(bl)
  }
}



