import { Component, OnInit } from '@angular/core';
import {FormControl, FormGroup, FormBuilder, Validators} from "@angular/forms";
import {mobileValida, passwordValida, emailValida, mobileAsyncValida, agreeValida} from "../../validators/validators";
import {LoginService} from "../login.service";
import {Router} from '@angular/router'
import {ToastrService} from 'ngx-toastr'
@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {

  private formModel:FormGroup;
  private fb:FormBuilder=new FormBuilder();
  private query:any={};
  private toastrActive:any;

  nameError:any = {};
  constructor(private loginService:LoginService,private router:Router,private toastr:ToastrService) {
    this.formModel=this.fb.group({
      name:['wanglinfei',[Validators.required,Validators.maxLength(11),Validators.minLength(3)]],
      mobile:['15801351602',[mobileValida],mobileAsyncValida],
      email:['977696449@qq.com',[emailValida]],
      passwordInfo:this.fb.group({
        password:['123456'],
        rePassword:['123456']
      },{validator:passwordValida}),
      agree:['',agreeValida]
    })
  }

  ngOnInit() {
  }
  nameBlur() {
    var nameErr=this.formModel.get('name').errors
    console.log(nameErr)
    if(!nameErr){
      this.nameError={}
      return;
    }
    if(nameErr.required){
      this.nameError={
        state:true,
        info:'用户名必填'
      }
    }else if(nameErr.minlength){
      this.nameError={
        state:true,
        info:'请输入3-11位用户名'
      }
    }
  }
  register(){
    var formValue=this.formModel.value;
    if(this.formModel.valid){
      this.query={
        name:formValue.name,
        mobile:formValue.mobile,
        email:formValue.email,
        pass:formValue.passwordInfo.password
      }
      this.loginService.register(this.query)
        .subscribe(res => {
          if(res.code==2011){
            this.toastrActive=this.toastr.info('你的号码已存在请去登录')
            this.toastrActive&&this.toastrActive.onHidden
              .subscribe(() => {
                this.router.navigateByUrl('/login')
              })
          }else if(res.code==0){
            localStorage.setItem('mobile',formValue.mobile)
            this.toastrActive=this.toastr.success('注册成功')
            this.toastrActive&&this.toastrActive.onHidden
              .subscribe(() => {
                this.router.navigateByUrl('/home')
              })
          }
        })
    }
  }
}



