import {CanDeactivate} from "@angular/router"
import { DashboardComponent } from '../first/dashboard.component'
export class focusGuard implements CanDeactivate<DashboardComponent> {
  canDeactivate(component:DashboardComponent) {
    if(!component.focus()){
      //return window.confirm('确定离开吗？')
      return true;
    }else{
      return true;
    }
  }
}



