///<reference path="first/hero.service.ts"/>
import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { NgModule } from '@angular/core';
import {FormsModule, ReactiveFormsModule}   from '@angular/forms';
import { HttpModule } from '@angular/http'
import { CalendarModule } from 'angular-calendar';
import { ToastrModule } from 'ngx-toastr';
import { NgbModalModule } from '@ng-bootstrap/ng-bootstrap';

import {PermissionGuard} from './guard/permission.guard'
import {focusGuard} from './guard/focus.guard'
import {DetailResolve} from './guard/detail.resolve'

import { firstComponent} from './first/first.component'
import { HeroDetailComponent } from './first/hero-detail.component';
import { HeroesComponent } from './first/heroes.component'
import { DashboardComponent } from './first/dashboard.component'
import { HeroService } from './first/hero.service';
import { HeroSearchComponent } from './first/hero-search.component'

import { AppRoutingModule }     from './app-routing.module';
import { AppComponent } from './app.component';
import { HeaderComponent } from './header/header.component';
import { FooterComponent } from './footer/footer.component';
import { MenuComponent } from './menu/menu.component';
import { SidebarComponent } from './sidebar/sidebar.component';
import { ContentComponent } from './content/content.component';
import { StockManageComponent } from './stock/stock-manage/stock-manage.component';
import { StarsComponent } from './stars/stars.component';
import { HomeComponent } from './home/home.component';
import { StockFormComponent } from './stock/stock-form/stock-form.component';
import {StockService} from "./stock/stock.service";
import { StockOtherComponent } from './stock/stock-other/stock-other.component';
import { StockPipe } from './stock/stock.pipe';
import { RegisterComponent } from './loginOrregister/register/register.component';
import { LoginComponent } from './loginOrregister/login/login.component';
import { MobileValideDirective } from './directive/mobile-valide.directive';
import {WebSocketService} from "./stock/webstocket.service";
import {LoginService} from "./loginOrregister/login.service";
import { ToastComponent } from './base-com/toast/toast.component';
import {LoginGuard} from "./guard/login.guard";
import {LoginResolve} from "./guard/login.resolve";

@NgModule({
  declarations: [
    firstComponent,
    HeroesComponent,
    HeroDetailComponent,
    DashboardComponent,
    HeroSearchComponent,
    AppComponent,
    HeaderComponent,
    FooterComponent,
    MenuComponent,
    SidebarComponent,
    ContentComponent,
    StockManageComponent,
    StarsComponent,
    HomeComponent,
    StockFormComponent,
    StockOtherComponent,
    StockPipe,
    RegisterComponent,
    LoginComponent,
    MobileValideDirective,
    ToastComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    AppRoutingModule,
    ReactiveFormsModule,
    BrowserAnimationsModule,
    CalendarModule.forRoot(),
    NgbModalModule.forRoot(),
    ToastrModule.forRoot({
      timeOut:2000,
      extendedTimeOut:1000,
      positionClass: 'toast-up-center',
      enableHtml:true,
      preventDuplicates: true,
    })
  ],
  providers: [HeroService,PermissionGuard,focusGuard,
              DetailResolve,StockService,WebSocketService,
              LoginService,LoginGuard,LoginResolve],
  bootstrap: [AppComponent]
})
export class AppModule {
}



