import {FormControl, FormGroup, FormArray} from "@angular/forms";
import {Observable} from "rxjs";

export function mobileValida(mobile:FormControl):any{
  let value=(mobile.value||'')+''
  if(!value){
    return {mobile:'手机号码不能为空'}
  }
  let reg=/^1[3|4|5|7|8][0-9]{9}$/;
  let valid = reg.test(value)
  return valid?null:{mobile:'手机号码错误'}
}
export function mobileAsyncValida(mobile:FormControl):any{
  return Observable.of(null).delay(3000);
}
export function passwordValida(passwordInfo:FormGroup):any{
  let password:FormControl=passwordInfo.get('password') as FormControl;
  let rePassword:FormControl=passwordInfo.get('rePassword') as FormControl;
  if(!password.value){
    return {password:'密码或确认密码不能为空'}
  }else if(!rePassword.value){
    return {password:'密码或确认密码不能为空'}
  }
  let valid=password.value ===rePassword.value;
  return valid?null:{password:'密码不一致'}
}
export function emailValida(email:FormControl):any{
  let value = email.value;
  if(!value){
    return {email:'邮箱不能为空'}
  }
  let reg=/^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
  let valid=reg.test(value);
  return valid?null:{email:'邮箱错误'}
}

export function agreeValida(agree:FormControl):any{
  if(agree.value){
    return null
  }
  return {agree:'请确认用户协议'}
}

export function categoriesValida(categories:FormArray):any{
  var valida = false;
  categories.controls.forEach((control) => {
    if(control.value){
      valida = true;
    }
  })
  if(valida){
    return null;
  }else{
    return {categories:'至少选择一项'}
  }
}



