
import {CanActivate} from "@angular/router"
export class PermissionGuard implements CanActivate {
  canActivate() {
    var hasPermission:boolean = Math.random() <0.7;
    if(!hasPermission){
     alert('无权访问')
    }
    return hasPermission;
  }
}



