import { Component, OnInit,OnDestroy,SimpleChanges,Output,Input,EventEmitter } from '@angular/core';
import {WebSocketService} from "../stock/webstocket.service";
import {Router} from '@angular/router'

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit,OnDestroy {
  messageNumb:number =1;
  time:any;
  timer:any = null;
  @Input()
  toggle:boolean;
  @Output()
  toggleEmit:EventEmitter<boolean> = new EventEmitter();

  constructor(private wsService:WebSocketService, private router:Router) { }

  ngOnInit() {
    this.time = new Date()
    this.newTimer()
    this.wsService.connect('ws://localhost:8082?userName=wanglinfei')
      .map(res => JSON.parse(res))
      .subscribe((data) => {this.messageNumb=data.messageNumb},
                  error => console.log(error),
                  () => console.log('服务器已断开'))
  }
  newTimer() {
    clearInterval(this.timer)
    this.timer=setInterval(() => {
      this.time=new Date()
    },1000)
  }
  toggleSide() {
    this.toggle=!this.toggle;
    this.toggleEmit.emit(this.toggle)
  }
  ngOnDestroy() {
    if(this.wsService.ws.readyState==1){
      this.wsService.ws.close();
      console.log(this.wsService.ws.readyState)
    }
  }
  singOut() {
    localStorage.removeItem('mobile')
    this.router.navigateByUrl('/login')
  }
}



