import { Component, OnInit,OnDestroy } from '@angular/core';
import {Observable} from "rxjs";
import {FormControl} from "@angular/forms";
import 'rxjs/Rx'
import {Hero} from "../../first/hero";
import 'rxjs/add/operator/toPromise'
import {Stock, StockService} from "../stock.service";
import {WebSocketService} from "../webstocket.service";

@Component({
  selector: 'app-stock-other',
  templateUrl: './stock-other.component.html',
  styleUrls: ['./stock-other.component.css']
})
export class StockOtherComponent implements OnInit,OnDestroy {
  private nameFilter:FormControl = new FormControl()
  private keyword:string='';
  private hero:Hero;
  stocks:Observable<Stock[]>;
  constructor(private stockSercive:StockService,private wsService:WebSocketService) { }
  ngOnInit() {
    Observable.from([1,2,3,4])
      .filter(e => e%2==0)
      .map(e => e*e)
      .subscribe((e) => {
        console.log(e)
      },(err) => {
        console.log(err)
      },() => {
        console.log('end')
      })
   this.nameFilter.valueChanges
     .debounceTime(500)
     .subscribe((value) => {
       this.keyword=value;
     })
  }
  getSearch(item:Hero) {
    this.hero=item;
  }
  wsServiceInit() {
    this.wsService.connect('ws://localhost:8082?userName=wlf')
      .map(res => JSON.parse(res))
      .subscribe(
        data => console.log(data.messageNumb),
        error => console.log(error),
        () => console.log('服务器已断开')
      )
  }
  connection() {
    if(this.wsService.ws.readyState!=1){
      this.wsServiceInit()
    }else{
      console.log('服务已连接不需要重新连接')
    }
  }
  send() {
    if(this.wsService.ws.readyState==1){
      this.wsService.send(this.keyword||'发送请求')
    }else{
      console.log('服务器已断开正在连接')
      this.wsServiceInit()
    }
  }
  close() {
    if(this.wsService.ws.readyState==1){
      this.wsService.ws.close();
    }
  }
  ngOnDestroy() {

  }
}



