import {NgModule} from '@angular/core'
import { RouterModule, Routes } from '@angular/router';
import {PermissionGuard} from './guard/permission.guard'
import {focusGuard} from './guard/focus.guard'
import {DetailResolve} from './guard/detail.resolve'

import { firstComponent} from './first/first.component'
import { HeroDetailComponent } from './first/hero-detail.component';
import { HeroesComponent } from './first/heroes.component'
import { DashboardComponent } from './first/dashboard.component'
import { HeroSearchComponent } from './first/hero-search.component'
import { StockManageComponent } from './stock/stock-manage/stock-manage.component';
import { HomeComponent } from './home/home.component';
import { StockFormComponent } from './stock/stock-form/stock-form.component';
import { StockOtherComponent } from './stock/stock-other/stock-other.component';
import {RegisterComponent} from "./loginOrregister/register/register.component";
import {LoginComponent} from "./loginOrregister/login/login.component";
import {LoginGuard} from "./guard/login.guard";
import {LoginResolve} from "./guard/login.resolve";

const routes: Routes =[
  {path:'first',
    component:firstComponent,
    children:[
    {path:'search',component:HeroSearchComponent,outlet:"aux"},
    {path:'', redirectTo:'/first/dashboard', pathMatch:'full'},
    {path: 'heroes', component: HeroesComponent},
    {path:'dashboard', component: DashboardComponent, canActivate:[PermissionGuard],canDeactivate:[focusGuard]},
    {path:'detail/:id', component:HeroDetailComponent,data:[{info:'aaaaa'}],resolve:{hero:DetailResolve}},
    {path:'heroSearch', component:HeroSearchComponent},
  ],resolve:{mobile:LoginResolve}},
  {path:'stockManage',component:StockManageComponent,resolve:{mobile:LoginResolve}},
  {path:'stock/:id',component:StockFormComponent,resolve:{mobile:LoginResolve}},
  {path:'home',component:HomeComponent,children:[
    {path:'', redirectTo:'/home', pathMatch:'full'},
  ]},
  {path:'register',component:RegisterComponent},
  {path:'login',component:LoginComponent},
  {path:'stockOther',component:StockOtherComponent,resolve:{mobile:LoginResolve}},
  {path:'**', redirectTo:'/home', pathMatch:'full'}
]
@NgModule({
  imports: [ RouterModule.forRoot(routes) ],
  exports: [ RouterModule ]
})
export class AppRoutingModule {}



