import { Injectable }             from '@angular/core';
import { Router, Resolve, RouterStateSnapshot,
         ActivatedRouteSnapshot } from '@angular/router';
import {Hero} from '../first/hero';
import {HeroService} from '../first/hero.service'
import { Location }  from '@angular/common';

@Injectable()
export class DetailResolve implements Resolve<Hero> {
  constructor(private heroService: HeroService, private router: Router,private location:Location) {}

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Promise<Hero> {
    let id = route.paramMap.get('id');

    return this.heroService.getHero(+id).then(hero => {
      if (hero.id>11) {
        return hero;
      } else {
        this.router.navigate(['/first/heroes']);
        return null;
      }
    });
  }
}



