import { Component, OnInit } from '@angular/core';
import {Stock, StockService} from "../stock.service";
import {ActivatedRoute, ParamMap, Router} from "@angular/router";
import {FormGroup, FormBuilder, Validators, FormControl} from "@angular/forms";
import {categoriesValida} from "../../validators/validators";
import {Observable} from 'rxjs'
@Component({
  selector: 'app-stock-form',
  templateUrl: './stock-form.component.html',
  styleUrls: ['./stock-form.component.css']
})
export class StockFormComponent implements OnInit {
  private categoriesList=['dev','test','prod'];
  private stock:Stock=new Stock(0,'',0,0,'',[]);
  private stockId:number;
  readonly:boolean = false;
  private formModel:FormGroup;
  constructor(private stockService:StockService,private routerInfo:ActivatedRoute, private router:Router) { }
  ngOnInit() {
    let fb=new FormBuilder();
    this.formModel=fb.group({
      name:['',[Validators.required,Validators.minLength(3)]],
      price:['',[Validators.required]],
      desc:[''],
      categories:fb.array([
        new FormControl(false),
        new FormControl(false),
        new FormControl(false)
      ],categoriesValida)
    })
    this.routerInfo.params.subscribe((params:ParamMap) => {
      this.stockId=parseInt(params['id'])
      if(!this.stockId){
        return;
      }
      this.stockService.getStock(this.stockId)
        .subscribe((res) => {
          this.stock=res
          this.formModel.reset({
            name:res.name,
            price:res.price,
            desc:res.desc,
            categories:[
              res.categories.indexOf(this.categoriesList[0])!=-1,
              res.categories.indexOf(this.categoriesList[1])!=-1,
              res.categories.indexOf(this.categoriesList[2])!=-1
            ]
          })
        })
    })

  }
  cancel() {
    this.router.navigateByUrl('/stockManage')
  }
  Preservation() {
    var chineseCategories = [];
    for(var i=0; i<this.categoriesList.length; i++){
      if(this.formModel.value.categories[i]){
        chineseCategories.push(this.categoriesList[i])
      }
    }
    this.formModel.value.categories=chineseCategories;
    this.formModel.value.rating=this.stock.rating;
    this.formModel.value.id = this.stock.id
    console.log(this.formModel.value)
    this.stockService.createStock(this.formModel.value)
      .then((res) => {
        if(res.code == 0){
          console.log(res)
          this.router.navigateByUrl('/stockManage')
        }
      })
  }
}



