import { Injectable } from '@angular/core';
import { Router, Resolve, RouterStateSnapshot,
  ActivatedRouteSnapshot } from '@angular/router';
import {Mobile} from "../stock/commonData";
import {ToastrService} from 'ngx-toastr'

@Injectable()
export class LoginResolve implements Resolve<Mobile> {
  private ActiveToaster:any;
  constructor(private router: Router,private toastr:ToastrService) {}

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Promise<Mobile> {
    return new Promise(resolve => {
      var mobile=localStorage.getItem('mobile');
      // console.log(mobile)
      if(!mobile){
        this.ActiveToaster=this.toastr.error('你还没登录请先登录','温馨提示',{timeOut:4000})
        this.ActiveToaster&&this.ActiveToaster.onHidden
          .subscribe(() => {

          })
        this.router.navigateByUrl('/login')
        resolve(null);
      }else{
        let obj=new Mobile(mobile)
        resolve(obj);
      }
    })
  }
}



