import { Injectable } from '@angular/core';
import {Http,Headers} from '@angular/http';
import {Observable} from 'rxjs'
import 'rxjs/add/operator/toPromise';
@Injectable()
export class StockService {
  private stock:Stock;
  private headers = new Headers({'Content-Type': 'application/json'});
  constructor(private http:Http) {}

  getStocks(query:any):Observable<any> {
    return this.http.post('/api/stockApi/list',JSON.stringify(query), {headers: this.headers})
      .map(response => response.json());
  }
  getStock(id:number):Observable<Stock> {
    return this.http.get('/api/stockApi/stock/'+id)
      .map(response => response.json().data as Stock);
  }
  createStock(stock:Stock):Promise<any>{
    return this.http.post('/api/stockApi/create',JSON.stringify(stock), {headers: this.headers})
      .toPromise()
      .then(res => res.json())
      .catch(this.handleError);
  }
  deleteStock(id:number):Promise<any>{
    return this.http.post('/api/stockApi/delete',{id:id})
      .toPromise()
      .then((res) => res.json())
      .catch(this.handleError);
  }
  private handleError(error: any): Promise<any> {
    console.error('An error occurred', error); // for demo purposes only
    return Promise.reject(error.message || error);
  }
}
export class Stock {
  constructor(public id: number,
              public name: string,
              public price: number,
              public rating: number,
              public desc: string,
              public categories: Array<string>) {
  }
}




