import { Directive } from '@angular/core';
import {NG_VALIDATORS} from "@angular/forms";
import {mobileValida} from "../validators/validators";

@Directive({
  selector: '[MobileValide]',
  providers:[{provide:NG_VALIDATORS,useValue:mobileValida,multi:true}]
})
export class MobileValideDirective {

  constructor() { }

}



